<template>
  <div>
    <div>
      <el-form size="mini" inline>
        <el-form-item label="报名类型">
          <el-input />
        </el-form-item>
        <el-form-item label="报名时间">
          <el-input />
        </el-form-item>
        <el-form-item>
          <el-button type="primary">查询</el-button>
        </el-form-item>
      </el-form>
      <el-table :data="myActivity" height="550" size="mini">
        <el-table-column prop="activeName" label="名称" align="center" />
        <el-table-column label="报名类型" align="center">
          <template slot-scope="scope">
            <!-- 秒杀:1, 团购：0 -->
            {{ scope.row.activeType == 0 ? '拼团' : '秒杀' }}
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="报名时间" align="center" />
        <el-table-column label="活动时间" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.startTime }} 至 {{ scope.row.endTime }}</p>
          </template>
        </el-table-column>
        <el-table-column label="活动状态" align="center">
          <template slot-scope="scope">
            <span v-if="new Date().getTime() < scope.row.startTimeDate">未开始</span>
            <span v-else-if="new Date().getTime() > scope.row.startTimeDate && new Date().getTime() < scope.row.endTimeDate">进行中</span>
            <span v-else-if="new Date().getTime() > scope.row.endTimeDate">已结束</span>
          </template>
        </el-table-column>
        <el-table-column label="详情" align="center" width="200">
          <template slot-scope="scope">
            <el-button v-if="scope" style="color: #ea1e1e; margin-right: 5px" type="text" @click="onOut(scope)">退出活动</el-button>
            <!-- <el-button style="color: #ea8d1e; margin-right: 5px" type="text">取消活动</el-button> -->
            <el-button type="text" @click="onConten(scope)">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="listTotal" @current-change="handleCurrentChange" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyActivity',

  data() {
    return {
      pageNo: 1, // 当前分页
      myActivity: [], // 我报名的活动列表
      listTotal: 0 // 我报名的活动列表总条数
    }
  },

  created() {
    this.apiGetMyActivity()
  },

  methods: {
    // 分页
    handleCurrentChange(pageNo) {
      this.apiGetMyActivity()
    },
    // 点击详情
    onConten(scope) {
      this.$router.push({ path: '/mark/plat/activity/myActivity/content', query: { id: scope.row.id }})
    },
    // 退出活动
    onOut(scope) {
      this.apiDropOut(scope)
    },

    // api---------------------------------------
    // 我报名的活动
    apiGetMyActivity() {
      const DATA = {
        pageNo: this.pageNo
      }
      this.$http.post('/woho/activity/apply/queryList', DATA)
        .then((res) => {
          this.myActivity = res.data.data.list
          this.listTotal = res.data.data.total
          // 把时间转换成时间戳再在对象里面添加进去
          this.myActivity.map((item) => {
            item.startTimeDate = new Date(item.startTime.replace(/-/g, '/')).getTime()
            item.endTimeDate = new Date(item.endTime.replace(/-/g, '/')).getTime()
          })
        })
    },
    // 退出活动
    apiDropOut (scope) {
      this.$http.post(`/woho/activity/apply/dropOut/${scope.row.id}`)
        .then((res) => {
          console.log(res)
          this.myActivity.splice(scope.$index, 1)
        })
    },
  }
}
</script>

<style lang="scss" scoped>

</style>
